/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #1b1b1b;
  font-size: 1.4rem;
  text-align: justify;
}
@media (min-width: 768px) {
  p {
    font-size: 1.8rem;
  }
}

.contenedor {
  width: 90%;
  max-width: 130rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  font-family: "Nunito Sans", sans-serif;
  margin: 1.25rem 0;
  font-weight: 400;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.contenido-centrado {
  max-width: 800px;
}

.sombra {
  -webkit-box-shadow: 0px 0px 28px 8px rgba(0, 0, 0, 0.18);
  -moz-box-shadow: 0px 0px 28px 8px rgba(0, 0, 0, 0.18);
  box-shadow: 0px 0px 28px 8px rgba(0, 0, 0, 0.18);
}

.plantilla {
  display: flex;
  flex-direction: column;
}

.wrapper {
  display: grid;
  grid-template-columns: 30% 70%;
}
@media (min-width: 768px) {
  .wrapper {
    grid-template-columns: 20% 80%;
  }
}

.barra-busqueda {
  height: 12rem;
  background-color: #00B0F0;
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  column-gap: 2rem;
}
.barra-busqueda input {
  width: 90%;
  height: 40%;
  margin: auto;
  border-radius: 1rem;
  border: none;
  text-align: center;
}
.barra-busqueda div {
  width: 100%;
  display: grid;
  gap: 0.2rem;
  grid-template-columns: repeat(2, 1fr);
}
.barra-busqueda div a {
  color: #FFFFFF;
  font-weight: 400;
  font-size: 2.4rem;
}
.barra-busqueda div a, .barra-busqueda div img {
  margin: auto 0;
}

/*
-----------------HEADER-----------------
*/
.header {
  height: 7rem;
  background-color: #1b1b1b;
  display: flex;
  align-items: center;
}
.header .contenedor {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .contenedor .navegacion a {
  background-color: #FFFFFF;
  border-radius: 2rem;
  border: none;
  padding: 0.5rem 1rem;
  font-size: 2rem;
  font-weight: 700;
  color: #00B0F0;
  margin-right: 4rem;
  transition: all 250ms ease;
}
.header .contenedor .navegacion a:last-of-type {
  margin-right: 0;
}
.header .contenedor .navegacion a:hover {
  background-color: #00B0F0;
  color: #FFFFFF;
}

/*
-----------------FOOTER-----------------
*/
.footer {
  height: 4rem;
  background-color: #1b1b1b;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer p {
  font-size: 1.6rem;
  color: #FFFFFF;
  margin: auto;
  text-align: center;
}

.pantalla-login {
  display: flex;
  justify-content: space-between;
  height: 90vh;
}
.pantalla-login .figura {
  flex: 0 0 calc(50% - 1rem);
  height: 100%;
  width: 50%;
  clip-path: polygon(25% 0%, 100% 0, 100% 90%, 75% 100%, 0 100%, 0% 10%);
  background-image: url("/build/img/login.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.pantalla-login .formulario {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background-color: #00B0F0;
  border-radius: 2rem;
  height: 80%;
  width: 30%;
  margin: auto;
  padding: 0 2rem;
}
.pantalla-login .formulario img {
  height: 5rem;
  width: auto;
  margin: 0 auto;
}
.pantalla-login .formulario p {
  text-align: center;
  margin: 0;
  font-weight: 700;
}
.pantalla-login .formulario .condiciones {
  font-size: 1rem;
  font-weight: 400;
}

.options {
  width: 80%;
  margin: 0 auto;
}
.options button, .options a, .options input {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 4rem;
  border: none;
  color: #1b1b1b;
  border-radius: 1rem;
  padding: 1rem auto;
  margin: 1rem auto;
  background-color: #FFFFFF;
  transition: all 250ms ease;
}
.options button:hover, .options a:hover, .options input:hover {
  cursor: pointer;
  background-color: #e9e9e9;
  font-weight: 700;
}
.options input {
  display: inline-block;
  text-align: center;
  margin: 0.5rem auto;
}
.options a {
  text-decoration: none;
}

/*
-----------------automoviles en la pagina principal-----------------
*/
.automoviles {
  margin: 1rem 2rem;
}
@media (min-width: 768px) {
  .automoviles {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.automovil {
  border: 1px solid grey;
}

.imagen-automovil {
  position: relative;
  display: inline-block;
  text-align: center;
}
.imagen-automovil .promocion, .imagen-automovil .agregar-a-favoritos {
  position: absolute;
  margin: 0;
  top: 0%;
}
.imagen-automovil .promocion {
  left: 0%;
  background-color: #00B0F0;
  color: #FFFFFF;
  padding: 0 1rem;
}
.imagen-automovil .agregar-a-favoritos {
  right: 0%;
}
.imagen-automovil .agregar-a-favoritos label {
  -webkit-text-stroke: 1px black;
  color: transparent;
  font-size: 4rem;
}
.imagen-automovil .agregar-a-favoritos label:hover {
  color: yellow;
}
.imagen-automovil .agregar-a-favoritos input[type=checkbox] {
  display: none;
}
.imagen-automovil .agregar-a-favoritos input[type=checkbox]:checked ~ label {
  color: orange;
}